.main {
  height: 100%;
  text-align: center;
}
.backdrop {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.3);
    z-index: 100;
}
.navbar {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 56px;
    background: #df4848;
}

.navbar__navigation {
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    padding-left: 1rem;
}

.navbar__title{
    color: white;
    font-size: 1.5rem;
    padding: 0 1rem;
    text-decoration: none;
}

.spacer {
    flex: 1 1;
}

.navbar__navigation-items {
    height: 100%;
}

.navbar__navigation-items ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.navbar__navigation-items li {
    padding: 16px 1rem;
}

.navbar__navigation-items li:hover,
.navbar__navigation-items li:active {
    background-color: #c2203b;
}

.navbar__navigation-items a {
    color: white;
    text-decoration: none;
}

@media (max-width: 768px) {
    .navbar__navigation-items {
        display: none;
    }
}

@media (min-width: 769px) {
    .navbar__toggle-button {
        display: none;
    }

    .navbar__title{
        padding: 0 0rem;
    }
}
.side-drawer {
    height: 100%;
    background: white;
    position: fixed;
    top: 0;
    left: 0;
    width: 70%;
    max-width: 300px;
    z-index: 200;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.side-drawer.open {
    box-shadow: 1px 0px 3px rgba(0, 0, 0, 0.5);
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.side-drawer__title-area {
    height: 56px;
    background: #df4848;
}

.side-drawer__title {
    color: white;
    font-size: 1.5rem;
    text-decoration: none;
    padding: 0.5rem 1rem;
    margin: 0;
}

.side-drawer ul {
    height: 100%;
    padding: 0 0;
    margin: 0 0;
    list-style: none;
    justify-content: center;
}

.side-drawer li {
    padding: 1rem 0;
    padding-left: 1rem;
    border-bottom: thin solid black;
    color: black;
}

.side-drawer li:hover,
.side-drawer li:active {
    background-color: #c2203b;
}

.side-drawer a {
    text-decoration: none;
    font-size: 1.2rem;
}

@media (min-width: 769px) {
    .side-drawer {
        display: none;
    }
}
.about-page__title {
    font-size: 3.5rem;
    margin-top: 10vh;
}

.about-page__card {
    margin: 5vh auto;
    background: white;
    border-radius: 50px;
    box-shadow: 1px 0px 3px rgba(0, 0, 0, 0.5);
}

@media (max-width: 768px) {
    .about-page__card {
        width: 90%;
    }
}

@media (min-width: 769px) {
    .about-page__card {
        width: 60%;
    }
}

.about-page__table {
    text-align: left;
    padding: 50px 20px;
    margin: 0 auto;
    font-size: 1.5rem;
}

.about-page__table tr {
    margin-bottom: 20px;
}

.about-page__icons {
    text-align: center;
}
@media (max-width: 768px) {
    .home-page__title {
        font-size: 2rem;
        margin-top: 20vh;
    }
}

@media (min-width: 769px) {
    .home-page__title {
        font-size: 3.5rem;
        margin-top: 40vh;
    }
}
.skills-page__title {
    font-size: 3.5rem;
    margin-top: 10vh;
}

.skills-page__container {
    max-width: 1200px;
    margin: 5vh auto;
}

.skills-page__card {
    max-width: 400px;
    height: 300px;
    margin-bottom: 30px;
}
.works-page__title {
    font-size: 3.5rem;
    margin-top: 10vh;
}

.works-page__container {
    max-width: 1200px;
    margin: 5vh auto;
}

.works-page__card {
    max-width: 400px;
    height: 300px;
    margin-bottom: 30px;
}

.works-page__source-link {
    position: absolute;
    bottom: 0;
    left: 35%;
    margin: 20px 0;
}
html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-image: url(/static/media/EchoCat.5ad68555.png);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: right bottom;
  background-size: 38% 38%;
}

